
import preview_picture from '@/components/template/preview_picture'
import zanImg from '../../../static/img/zan@2x.png'
import zanedImg from '../../../static/img/zan-selected@2x.png'
import collectImg from '../../../static/img/shoucang-@2x.png'
import collectedImg from '../../../static/img/shoucang-selected_yellow@2x.png'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default{
name: "discover_index",
data(){
return{
zanImg     : zanImg,
zanedImg   : zanedImg,
collectImg : collectImg,
collectedImg: collectedImg,
pageWidth  : null,			//屏幕宽度
category   : null,			//分类数据
categoryIdx: 0,				//分类选择的默认下标
categoryId : 1,				//分类的id
userId     : null,			//用户id
circleList : [],			//资讯数据
page_num   : 1,				//加载页数
newData    : [],			//新加载的数据
noData     : null,
previewShow: false,			//是否显示图片预览
previewImg : null,			//预览图片的路径
}
},
components:{preview_picture, Tab, TabItem},
created(){
this.userId = this.$route.query.uid?this.$route.query.uid:'';
this.pageWidth = window.screen.availWidth;
this.$http.get(process.env.BASE_URL+"/apps/circle/category",{
params:{uid: this.userId}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.category = res.body.list
}
});

this.getData(this.categoryId, this.userId, this.page_num)
},
methods:{
onItemClick(id){
this.categoryId = id;
this.circleList = [];
this.page_num = 1;
this.getData(this.categoryId, this.userId, this.page_num)
},

toCompany(id){
this.$router.push({path:"/space_detail", query:{cid: id}})
},

lookImg(src){

this.previewShow = true;
this.previewImg = src;
},

previewHide(){
this.previewShow = false;
},

call(phone){
location.href="tel:"+phone;
},

praise(pid, code, index){
this._praise(pid, code, index)
},

collect(id, content, code, index){
if(this.categoryId==1 || this.categoryId==2 || this.categoryId==3){			//讯息
this.collect_focus(5, id, content, code, index)
}else if(this.categoryId==4 || this.categoryId==5){		//资讯
this.collect_focus(1, id, content, code, index)
}
},

publish(){
this.$router.push({path: "/publish"})
},

detail_message(id){
this.$router.push({path: "/detail_message", query:{id:id}})
},

detail_news(id){
this.$router.push({path: "/detail_news", query:{id:id}})
},

_praise(pid, code, index){
var Url;
if(code==40007){
if(this.categoryId==1 || this.categoryId==2 || this.categoryId==3){
Url = process.env.BASE_URL+"/apps/circle/praiseAdd";
}else if(this.categoryId==4 || this.categoryId==5){
Url = process.env.BASE_URL+"/apps/news/praiseAdd";
}
}else{
if(this.categoryId==1 || this.categoryId==2 || this.categoryId==3){
Url = process.env.BASE_URL+"/apps/circle/praiseDel"
}else if(this.categoryId==4 || this.categoryId==5){
Url = process.env.BASE_URL+"/apps/news/praiseDel";
}
}
this.$http.post(Url, {
pid: pid,
}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
if(code==40007){
this.circleList[index].praise_code = 40008;
this.circleList[index].praise_count ++;
}else{
this.circleList[index].praise_code = 40007;
this.circleList[index].praise_count --;
}
}
})
},

collect_focus(type, id, title, code, index){
var Url;
if(code==40005){
Url = process.env.BASE_URL+"/apps/general/collectAdd";
}else{
Url = process.env.BASE_URL+"/apps/general/collectDel"
}

this.$http.post(Url,{
aid	  : id,
title : title,
favorite_category: type,
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
if(code==40005){
this.circleList[index].collect_code = 40006;
}else{
this.circleList[index].collect_code = 40005;
}
}
})
},
infinite(done) {


console.log(this.newData,"-9-9-9-9-")
if(this.newData.length==0) {
setTimeout(()=>{
this.$refs.myscroller.finishInfinite(2);
})
return ;
}
this.page_num ++;
let self = this;

setTimeout(() => {
this.getData(this.categoryId, this.userId, this.page_num)
if(this.newData.length==0) {
self.noData = "没有更多数据"
}
self.$refs.myscroller.resize();
done()
}, 500)

},
refresh(done) {
console.log('refresh')
setTimeout(() => {
done()
}, 10)
},
getData(cid,uid,page){
this.$http.get(process.env.BASE_URL+"/apps/circle/indexNew",{
params:{
cid : cid,
uid : uid,
page: page
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
if(cid==4||cid==5){					//资讯
this.newData = res.body.list.newslist;
}else{								//讯息、关注
this.newData = res.body.list.circle_list;
}
for(var i=0; i<this.newData.length; i++){
this.circleList.push(this.newData[i])
}
}else{
this.newData = []

}
})
}
}
}



