
import header_nav from '@/components/template/header'
import uploader from '@/components/template/t_uploader.vue'
import backImg from '../../../static/img/fanhui-@2x.png'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default{
name: "company_spaceport",
data(){
return{
type      : null   ,
goods_category : "" ,
product_category : "",  //后台返的产品分类数据
plainList : [], //产品分类 数据
list_section:{ //空间站列表信息
owner_name:null,    //联系人姓名
phone1:null,        //手机号
phone2:null, 		//电话
address:null,		//联系人地址
owner_pic:null,		//联系人照片
name:null,  		//公司名称
category_id:null,   //产品分类id
business:null,  	//公司主营业务
summary:null,    	//公司简介
company_license_pic:null,//公司营业执照图片
space_price_id:null,		//所选费用的id
agree:null,		//同意xxxx协议，不勾选就没有这个值
},
showScrollBox:false, //是否显示协议
agreement:null, //协议
space_price_id: [] , //资费信息列表
nav_header: [
{text: '', icon:backImg, type:'back', class:'header_left'},
{text: '企业空间站开通', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
], //头部数据
getImageUrl:null,
upImageUrl   : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
agree:false,    //年费协议
not_field:null, //x-input 不需要绑定数据 可用该字段
}
},
components: {header_nav,Group,XInput,XTextarea,Selector,Radio,XButton,XDialog,uploader},
created(){
var that = this ;
this.$http.get(process.env.BASE_URL+"/apps/member/openCompanySpace").then(function(res){
that.product_category = res.body.list.category;

for(var i in that.product_category){
that.plainList.push({key:that.product_category[i].id, value: that.product_category[i].name});
}
that.agreement = res.body.list.agreement;
that.list_section = res.body.list.section;
that.list_section.agree = 0;
that.space_price_id = res.body.list.space_price_id;
});
},
watch: {
agree(val,oldval){
if(val){
this.list_section.agree = 1;
}else{
this.list_section.agree = 0;
}
}
},
methods:{
/**
* 选择产品分类
*/
changeType:function(value){
this.list_section.category_id = value;
},

getImgUrl(url){
this.list_section.owner_pic = url;
},
removeImg0(index){
this.list_section.owner_pic = "";
},
/**
* 公司营业执照图片
*/
getImgUrl1(url){
this.list_section.company_license_pic = url;
},
removeImg1(index){
this.list_section.company_license_pic = "";
},

publish_message:function(){
var that = this;
this.$http.post(process.env.BASE_URL+'/apps/member/openCompanySpace',that.list_section).then(function(res){
if(res.body.code=="40000"){
var btnTitle = res.body.list.btnTitle;
var product_id = res.body.list.product_id;
that.$router.push({path: 'not_payment', query:{type: 2, btnTitle:btnTitle,product_id:product_id}});
}else{
that.$vux.toast.text(res.body.hint, 'middle');
}
});
},
}
}



