import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'  
import defaultImg from '../../../static/img/kongjianzhan-wkt@2x.png' 
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'back', class:'header_left'},
				{text: '个人空间站', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'', class:'header_right'},
			], 
			type:0, //0.个人空间 1.企业空间  
			btnTitle:null, //按钮标题
			text:null,
			defaultImg:defaultImg, //默认图片
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		this.type = this.$route.query.type; 
		this.btnTitle = this.$route.query.btnTitle;
		this.text = this.$route.query.text;
		if(this.type==0){
			this.nav_header[1].text ="个人空间站";
		}else{ 
			this.nav_header[1].text ="企业空间站";
		}
	},
	methods:{
		goLocation(){
			if(this.type==0){
				this.$router.push({path: "/personal_space"}); //个人空间开通
			}else{
				this.$router.push({path: "/company_spaceport"}); //企业空间开通
			}
		}
	}
}



