
import header_nav from '@/components/template/header'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default{
name:"space_index",
data(){
return {
nav_header: [
{text: '', icon:'', type:'', class:'header_left'},
{text: '移动空间站', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
category    : 0,				//分类
categoryNav : null,				//分类数据
company_list: [],				//公司列表数据
page_num    : 1,				//加载页数
newData     : [],				//新加载的数据
noData      : null,
}
},
components:{header_nav, Tab, TabItem},
created(){

this.$http.get(process.env.BASE_URL+"/apps/company/category").then(function(res){
console.log(res)
if(res.body.code==40000){
this.categoryNav = res.body.list.category;
}
});

this.getCompanyList(this.page_num, this.category)
},
methods:{
onItemClick(category_id){

this.page_num = 1;
this.category = category_id;
this.company_list = [];
this.getCompanyList(this.page_num, this.category)
},
detail(id){
this.$router.push({path: "/space_detail", query: {cid: id}})
},
infinite(done) {


if(this.newData.length==0) {
setTimeout(()=>{
this.$refs.myscroller.finishInfinite(2);
})
return;
}
this.page_num ++;
let self = this;

setTimeout(() => {
this.getCompanyList(this.page_num, this.category)
if(this.newData.length==0) {
self.noData = "没有更多数据"
}
self.$refs.myscroller.resize();
done()
}, 500)

},
refresh(done) {
console.log('refresh')
setTimeout(() => {
done()
}, 10)
},
getCompanyList(page, category){
this.$http.get(process.env.BASE_URL+"/apps/company/index",{
params:{
category: category,
page: page
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){

this.newData = res.body.list.company_list;
for(var i=0; i<this.newData.length; i++){
this.company_list.push(this.newData[i])
}
}
})
}
}
}



