
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default{
name:"news_collection",
data(){
return{
nav_header: [
{text: '', icon:backImg, type:'back', class:'header_left'},
{text: '个人信息修改', icon: '', type:'', class:'header_middle'},
{text: '管理', icon: '', type:'userSave', class:'header_right'},
],
list:{
username:null,
},
uid:null,
status:0, //编辑状态 0.预览状态 1.编辑状态
name:null,
imgBase64:null,
head_pic:null,
upImageUrl   : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
isPost:false, //是否可以提交 默认不可提交 修改内容后才能修改
}
},
mounted () {

},
components: {header_nav,Group,XInput,Cell},
created(){
this.uid = this.$route.query.uid;
this.getData();
},
watch: {
name(val,oldval){
this.isPost = true;
},
headImg(val,oldval){
this.isPost = true;
}
},
methods:{
/**
* 获取数据
*/
getData(){
var that = this;
this.$http.get(process.env.BASE_URL+"/apps/circle/memberInfo",{
params:{
uid    : that.uid
}
}).then(function(res){
if(res.body.code==40000){
that.imgBase64 = that.head_pic = res.body.list.memberInfo.head_pic;
that.name = res.body.list.memberInfo.name;
}
});

},
userInfoSave(){
if(this.status==0){
this.status = 1;
this.nav_header[2].text = "保存";
}else{
this.saveUserInfo();
}
},
saveUserInfo(){
var that = this;
if(that.isPost){
var data = {head_pic:that.head_pic,name:that.name};
this.$http.post(process.env.BASE_URL+'/apps/member/memberInfoEdit',data).then(function(res){
if(res.body.code=="40000"){
that.$vux.toast.text(res.body.hint, 'middle');
setTimeout(function(){
that.status = 0;
that.nav_header[2].text = "管理";
},1000);
}else{
that.$vux.toast.text(res.body.hint, 'middle');
}
});
}
},
/**
* 注销-退出账号
*/
logout(){
localStorage.token="";
this.$router.push({path:"login"});
},
/**
* 选择头像
*/
selHead(){
this.$refs.file.click();
},
/**
* 选择文件
*/
changeFile(){
var that = this;
const files = this.$refs.file.files;
const xhr = new XMLHttpRequest();
const formData = new FormData();
formData.append(files[0].name,files[0]);
xhr.open('POST', that.upImageUrl, true);
xhr.send(formData);
xhr.onload = function(){
that.$vux.toast.text("图片上传中...", 'middle');
if (xhr.status === 200 || xhr.status === 304) {
var imgUrl = JSON.parse(xhr.response).url;
that.$vux.toast.text("图片上传完成", 'middle')
that.head_pic = imgUrl;
} else {
console.log(`error：error code ${xhr.status}`)
}
}
const reader = new FileReader();
reader.onload = function(e){
that.head_pic = "";
that.imgBase64 = e.target.result;
}
reader.readAsDataURL(files[0]);
},
}
}



