	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import defaultImg from '../../../static/img/daixvfei@2x.png' 
	import cpIcon from '../../../static/img/yiguanzhu@2x.png'  
	import notColl from '../../../static/img/wushangpinshoucang@2x.png'   
	export default{
		name:"news_collection",
		data(){
			return{
				nav_header: [
					{text: '', icon:backImg, type:'back', class:'header_left'},
					{text: '关注的人', icon: '', type:'', class:'header_middle'},
					{text: '', icon: '', type:'', class:'header_right'},
				], 
				list:[], //关注的人数据
				newData:[], //当前调用接口获取的数据
				defaultImg:defaultImg,
				cpIcon:cpIcon,
				page_num:1,
				noData:false, //是否有数据
				notColl:notColl, //没有收藏图片
			}
		},
		mounted () { 
	  	},
		components: {header_nav},
		created(){ 
			this.getData();
		},
		methods:{
			getData(){
				var that = this;
				this.$http.get(process.env.BASE_URL+"/apps/circle/focusList",{
					params:{
						page    : that.page_num
					}
				}).then(function(res){
					if(res.body.code==40000){
						that.newData = res.body.list.focus_list;
						if(that.newData.length==0 && that.page_num ==1){
							that.noData = true;
						}
						for(var i=0;i<that.newData.length;i++){
							that.list.push(that.newData[i]);
						}
					}
				}) 
			},
			/**
			 * 跳转关注的人详情 
			 */
			goLocation(uid){
				this.$router.push({path:"/cperson_detail",query:{uid:uid}});
			},	
			infinite(done) { 
				let that = this; 
                if(that.newData.length==0) {
	                setTimeout(()=>{
	                    that.$refs.myscroller.finishInfinite(2);
	                })
	                return;
                }
                that.page_num ++; 
				
                setTimeout(function(){
                	that.getData();	
                    // if(that.newData.length==0) {
                    //     that.noData = "没有更多数据";
                    // }
                    that.$refs.myscroller.resize();
                    done();
                }, 500); 
            },
            refresh(done) {
          		setTimeout(function(){
		          	done();
		        }, 10);
            },
		}
	}



