
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default{
name: "supply_demand",
data(){
return{
uid         : null,				//用户id
category    : null,
type        : null,
categoryNav : null,				//分类列表
category_idx: 0,				//分类下标
supply_list : [],				//求购列表
nav_header  : [
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '供求信息', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
page_num    : 1,				//加载页数
loading_ani : false,			//加载动画判断
newData     : [],				//新加载的数据
noData      : null,
}
},
components: {header_nav, LoadMore},
created(){
console.log(this.$route.query)
this.uid = this.$route.query.uid;
this.category = this.$route.query.category;
this.type = this.$route.query.type;
this.category_idx = Number(this.$route.query.index)+1;
this.getData(this.page_num)
},
methods:{
address(address){
console.log(address,"--")
},
categoryClick(index, id, type){
this.page_num = 1;
this.supply_list = [];
this.category_idx = index;
this.category = id;
this.type = type;
this.getData(this.page_num)
},
detail(id, type){
this.$router.push({path:"/info_detail", query:{id: id, type: type}})
},
getData(page){
this.loading_ani = true;			//加载动画
this.$http.get(process.env.BASE_URL+"/apps/supply/index",{
params:{
category: this.category,
type    : this.type,
uid     : this.uid?this.uid:'',
page    : page
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.loading_ani = false;  	//加载动画
this.categoryNav = res.body.list.category;

this.newData = res.body.list.supply_list;
for(var i=0; i<this.newData.length; i++){
this.supply_list.push(this.newData[i])
}

}else if(res.body.code==70000){
this.$router.push({path:"/index"})
}
})
},
infinite(done) {

if(this.newData.length==0) {
setTimeout(()=>{
this.$refs.myscroller.finishInfinite(2);
})
return;
}
this.page_num ++;
let self = this;

setTimeout(function() {
self.getData(self.page_num);
if(self.newData.length==0) {
self.noData = "没有更多数据"
}
self.$refs.myscroller.resize();
done()
}, 500)

},
refresh(done) {
console.log('refresh')
setTimeout(() => {
done()
}, 10)
},
}
}



