
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'

import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default{
name: "detail",
data(){
return{
item_id     : null,				//详情的id值
item_type   : null,				//详情类型
category    : null,				//分类标识
header_img  : null,				//用户头像
nick_name   : null,				//昵称
img_detail  : null,				//详情图片
beginAddress: null,				//起始地
endAddress  : null,				//目的地
user_id     : null,				//用户id
collectCode : null,				//收藏码
collectTitle: null,				//收藏标题
focusCode   : null,				//关注码
focusTitle  : null,				//关注标题
link_url    : null,				//联系他跳转路径
nav_header:[
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '信息详情', icon: '', type:'', class:'header_middle'},
{text: '联系他', icon: '', type:'link', class:'header_right'},
],

group_data1 : [
{title: "信息标题：", value: ""},
{title: "企业名称：", value: ""},
{title: "联系人：" , value: ""},
{title: "联系电话：", value: "", type: "phone"},
],								//第一组数据
group_data2 : [
{title: "仓储地址：", value: "", type:"input"},
{title: "储存面积：", value: "", type:"input"},
{title: "服务内容：", value: "", type:"textarea"},
],								//第二组数据
group_data3 : [
{title: "车牌号：", value: ""},
{title: "车载吨位：", value: ""},
{title: "可载数量：", value: ""},
{title: "服务时间：", value: ""},
],								//第三组数据
group_data4 : [
{title: "可载数量：", value: ""},
{title: "服务时间：", value: ""},
],								//第四组数据

group_data5 : [
{title: "信息标题：", value: ""},
{title: "货物名称：", value: ""},
{title: "是否危险品：", value: ""},
{title: "货物数量：", value: ""},
],								//第五组数据
group_data6 : [
{title: "联系人：", value: ""},
{title: "联系电话：", value: "", type: "phone"},
],								//第六组数据
group_data7 : [
{title: "服务时间：", value: ""},
{title: "运输地点：", value: ""},
],								//第七组数据
group_data8 : [
{title: "服务种类：", value: ""},
{title: "仓储面积：", value: ""},
{title: "服务内容：", value: ""},
],								//第八组数据
group_data9 : [
{title: "服务种类：", value: ""},
{title: "服务内容：", value: ""},
],								//第九组数据
}
},
components: {header_nav,Group, Cell, CellBox, XTextarea},
created(){
console.log(this.$route.query)
this.item_id = this.$route.query.id;
this.item_type = this.$route.query.type;
this.$http.get(process.env.BASE_URL+"/apps/supply/detail",{
params:{
id: this.item_id
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.category = res.body.list.category;			//分类
this.header_img = res.body.list.head_pic;		//头像
this.nick_name = res.body.list.username;		//昵称
this.img_detail = res.body.list.image			//图片详情
this.user_id = res.body.list.uid;				//用户id
this.collectCode = res.body.list.collected;		//收藏码
this.collectTitle = res.body.list.title;		//收藏标题
this.focusCode = res.body.list.followed;		//关注码
this.link_url = res.body.list.real_href;		//联系他跳转路径

if(this.item_type==1){
this.beginAddress = res.body.list.regions1;
this.endAddress = res.body.list.regions2;

this.group_data1[0].value = res.body.list.title;
this.group_data1[1].value = res.body.list.company;
this.group_data1[2].value = res.body.list.name;
this.group_data1[3].value = res.body.list.mobile;

if(this.category==122){
this.group_data2[0].value = res.body.list.regions1+""+res.body.list.address;
this.group_data2[1].value = res.body.list.depot_area+"平米";
this.group_data2[2].value = res.body.list.depot_content;
}

if(this.category==124){
this.group_data3[0].value = res.body.list.car_number;
this.group_data3[1].value = res.body.list.car_load+"吨";
this.group_data3[2].value = res.body.list.max_load+"吨";
this.group_data3[3].value = res.body.list.time1+" 至 "+res.body.list.time2;
}

if(this.category==118||this.category==119||this.category==120||this.category==121){
this.group_data4[0].value = res.body.list.max_load+"吨";
this.group_data4[1].value = res.body.list.time1+" 至 "+res.body.list.time2;
}
}else if(this.item_type==2){

this.group_data5[0].value = res.body.list.title;
this.group_data5[1].value = res.body.list.goods_name;
this.group_data5[2].value = res.body.list.is_danger==1?"是":"否";
this.group_data5[3].value = res.body.list.goods_num;

this.group_data6[0].value = res.body.list.name;
this.group_data6[1].value = res.body.list.mobile;

this.group_data7[0].value = res.body.list.time1;
this.group_data7[1].value = res.body.list.regions1+" "+res.body.list.address;

if(this.category==122){
this.group_data8[0].value = res.body.list.category_name;
this.group_data8[1].value = res.body.list.depot_area+"平米";
this.group_data8[2].value = res.body.list.depot_content;
}else{
this.group_data9[0].value = res.body.list.category_name;
this.group_data9[1].value = res.body.list.depot_content;
}
}
}
})
},
methods:{

link(){
location.href=this.link_url;
},

toSpace(){
console.log("空间")
},

collect_focus(type, id, title, code){
var Url;
if(code==0){
Url = process.env.BASE_URL+"/apps/general/collectAdd";
}else{
Url = process.env.BASE_URL+"/apps/general/collectDel"
}

this.$http.post(Url,{
aid	  : id,
title : title,
favorite_category: type,
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
if(code==0){
if(type==6){
this.collectCode = 1;
}else{
this.focusCode = 1
}
}else{
if(type==6){
this.collectCode = 0;
}else{
this.focusCode = 0
}
}
}
})
},

call(type, number){

if(type=='phone'){
location.href="tel:"+number;
}
}
}
}



