
import header_nav from '@/components/template/header'
import uploader from '@/components/template/uploader-1.vue'
import backImg from '../../../static/img/fanhui-@2x.png'
import selectImg from '../../../static/img/ap-weixuan@2x.png'
import selectedImg from '../../../static/img/sp-xuanzhong@2x.png'
import XButton from 'vux/src/components/x-button/index.vue'


export default{
name: "company_album",
data(){
return{
nav_header: [
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '空间相册', icon: '', type:'', class:'header_middle'},
{text: '删除', icon: '', type:'delet', class:'header_right'},
],
selectImg  : selectImg,
selectedImg: selectedImg,
album      : [],		//相册数据
page_width : null,		//页面宽度
upImageUrl : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
imagesData : [],
showSubmit : true,		//隐藏完成按钮
}
},
components:{header_nav, uploader, XButton},

created(){
var id = this.$route.query.id;
this.page_width = window.screen.availWidth;
this.$http.get(process.env.BASE_URL+"/apps/company/album",{
params:{ id : id }
}).then(function(res){
console.log(res)
if(res.body.code==40000){

this.imagesData = res.body.list.album;

for(var i=0; i<res.body.list.album.length; i++){
var imgs = res.body.list.album[i];
this.album.push({check: 0, img_url: imgs})
}
}
console.log(this.album, this.imagesData)
})
},
methods:{
del(){
if(this.imagesData.length == this.album.length){
this.$vux.toast.text('请选择要删除的项', 'middle')
return false;
}

this.submitImg()
},
selectEve(idx, checkurl){
console.log(idx)
this.imagesData = [];

this.album[idx].check = this.album[idx].check==1?0:1;

for(var i=0; i<this.album.length; i++){
if(this.album[i].check==0){
this.imagesData.push(this.album[i].img_url)
}
}

if(this.imagesData.length != this.album.length){
this.showSubmit = false
}else{
this.showSubmit = true
}

console.log(this.album, this.imagesData,"sss")
},

getImgUrl(_url){
this.album.unshift({check: 0, img_url: _url})

this.imagesData.unshift(_url)
console.log(this.album, this.imagesData)
},

submitImg(){
console.log(this.imagesData)
this.$http.post(process.env.BASE_URL+"/apps/company/albumadd", {
id   : this.$route.query.id,
album: this.imagesData.join()
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
this.$router.push({path:"company_album", query:{id: this.$route.query.id}})
}
})
}
},
}



