
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import uploader from '@/components/template/t_uploader.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

export default{
name:"news_collection",
data(){
return{
nav_header: [
{text: '', icon:backImg, type:'back', class:'header_left'},
{text: '意见反馈', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
list:{
content:null,
phone:null,
img:[],
},
upImageUrl   : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
not_field:null,
}
},
mounted () {
},
components: {header_nav,XTextarea,Group,XInput,uploader},
created(){

},
methods:{
/**
* 发布
*/
submit(){
var that = this;
this.$http.post(process.env.BASE_URL+'/apps/member/opinion',that.list).then(function(res){
if(res.body.code=="40000"){
that.$vux.toast.text(res.body.hint, 'middle');
setTimeout(function(){
that.$router.push({path: 'about_app'});
},1000);
}else{
that.$vux.toast.text(res.body.hint, 'middle');
}
});
},
/**
* 删除图片
* @param  {[int]} index [数组索引]
*/
removeImg0(index){
this.list.img.splice(index,1);
},
/**
* 图片选择回调函数
*/
getImgUrl(url){
this.list.img.push(url);
}
}
}



