

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import router from './router'
import VueScroller from 'vue-scroller'





import App from './App'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import Toast from 'vux/src/components/toast/index.vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadMore from 'vux/src/components/load-more/index.vue'


Vue.http.options.emulateJSON = true;		//post请求必加参数


Vue.use(VueRouter)
Vue.use(VueScroller)

Vue.use(AlertPlugin) //弹出框
Vue.use(ToastPlugin) //提示框
Vue.use(Actionsheet) //活动工作表
Vue.use(LoadingPlugin) //loading加载动画
Vue.use(Toast) //信息提示
Vue.use(ConfirmPlugin) //询问框
Vue.use(LoadMore) //加载更多

FastClick.attach(document.body)

/*设置请求头部*/
Vue.http.options.emulateHTTP = true;
Vue.http.options.emulateJSON = true;
Vue.config.productionTip = false


Vue.http.interceptors.push(function(request, next) {
request.headers.set('token', localStorage.token);

next((res) => {

if(res.body.code != "40000") {
this.$vux.toast.text(res.body.hint, 'middle')
}
});
});

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



