
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import defaultImg from '../../../static/img/daixvfei@2x.png'
import uploader from '@/components/template/t_uploader.vue'
import imgupload from '@/components/template/imgupload.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default{
name:"news_collection",
data(){
return{
nav_header: [
{text: '', icon:backImg, type:'back', class:'header_left'},
{text: '个人空间站开通', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
section:{
owner_name     : null,    		//联系人姓名
phone1	       : null,        //手机号
phone2         : null, 				//电话
address        : null,				//联系人地址
owner_pic      : null,				//联系人照片
id_card_pic1   : null,  		//身份证正面
id_card_pic2   : null,  		//身份证反面
card_in_hand   : null,  		//手持身份证照片
car_number     : null,    	//车牌号
car_license_pic: null,			//车辆牌照照片
car_pic1       : null,			//车辆照片
car_pic2       : null,			//车辆照片
car_pic3       : null,			//车辆照片
driver_license_pic : null,	//车辆行驶证照片
category_id    : null,			//产品分类id
summary        : '',				//个人简介
space_price_id : null,			//所选资费的id
agree          : null,			//同意xxxx协议，不勾选就没有这个值
},
showScrollBox    : false,
agreement        : null, 			//协议
upImageUrl       : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
defaultValue     : '',
summary          : '',				//个人简介
agree            : false,    	//年费协议
space_price_id   : [] , 			//资费信息列表
plainList        : [], 				//产品分类 数据
product_category : "",  			//后台返的产品分类数据
not_field        : null,			//x-input 不需要绑定数据 可用该字段
}
},
components: {header_nav,Group,XInput,XTextarea,Selector,Radio,XButton,XDialog,uploader,imgupload},
created(){
var that = this ;
this.$http.get(process.env.BASE_URL+"/apps/member/openPersonSpace").then(function(res){
if(res.body.code=="40000"){
that.section = res.body.list.section;
that.section.agree = 0;
that.space_price_id = res.body.list.space_price_id;
that.product_category = res.body.list.category;
that.agreement = res.body.list.agreement;

for(var i in that.product_category){
that.plainList.push({key:that.product_category[i].id, value: that.product_category[i].name})
}
}
});
},
watch: {
summary(val,oldval){
this.section.summary = val;
},
agree(val,oldval){
if(val){
this.section.agree = 1;
}else{
this.section.agree = 0;
}
}
},
methods:{
userInfoSave(){
},
/**
* 获取联系人照片
*/
getImgUrl(url){
this.section.owner_pic = url;
},
removeImg0(index){
this.section.owner_pic = '';
},
/**
* 获取正面身份证照
*/
getImgUrl1(url){
this.section.id_card_pic1 = url;
},
removeImg1(index){
this.section.id_card_pic1 = '';
},
/**
* 获取反面面身份证照
*/
getImgUrl2(url){
this.section.id_card_pic2 = url;
},
removeImg2(index){
this.section.id_card_pic2 = '';
},
/**
* 手持身份证照片
*/
getImgUrl3(url){
this.section.card_in_hand = url;
},
removeImg3(index){
this.section.card_in_hand = '';
},
/**
* 车牌照片：
*/
getImgUrl4(url){
this.section.car_license_pic = url;
},
removeImg4(index){
this.section.car_license_pic = '';
},
/**
* 车辆照片：
*/
getImgUrl5(url){
if(this.section.car_pic1!="" && this.section.car_pic2!="" && this.section.car_pic3==""){
this.section.car_pic3 = url;
}else if(this.section.car_pic1!="" && this.section.car_pic2 ==""){
this.section.car_pic2 = url;
}else{
this.section.car_pic1 = url;
}
},
removeImg5(index){
if(this.section.car_pic1!="" && this.section.car_pic2!="" && this.section.car_pic3!=""){
this.section.car_pic3 = '';
}else if(this.section.car_pic1!="" && this.section.car_pic2 !=""){
this.section.car_pic2 = '';
}else{
this.section.car_pic1 = '';
}
},
/**
* 车辆行驶证照片：
*/
getImgUrl6(url){
this.section.driver_license_pic = url;
},
removeImg6(index){
this.section.driver_license_pic = '';
},
/**
* 选择产品分类
*/
changeType:function(value){
this.section.category_id = value;
},
/**
* 提交数据
*/
publish_message(){
var that = this;
this.$http.post(process.env.BASE_URL+'/apps/member/openPersonSpace',that.section).then(function(res){
if(res.body.code=="40000"){
var btnTitle = res.body.list.btnTitle;
var product_id = res.body.list.product_id;
that.$router.push({path: 'not_payment', query:{type: 1, btnTitle:btnTitle,product_id:product_id}});
}else{
that.$vux.toast.text(res.body.hint, 'middle');
}
});
}
}
}



