
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
name: "company_album",
data(){
return{
nav_header: [
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '空间相册', icon: '', type:'', class:'header_middle'},
{text: '管理', icon: '', type:'modify', class:'header_right'},
],
album      : [],		//相册数据
page_width : null,		//页面宽度
edit_code  : null,		//修改码
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
components:{header_nav, Previewer},

created(){
var id = this.$route.query.id;
this.page_width = window.screen.availWidth;
this.$http.get(process.env.BASE_URL+"/apps/company/album",{
params:{ id : id }
}).then(function(res){
console.log(res)
this.edit_code = res.body.list.edit_code;

if(this.edit_code=="40011"){
this.nav_header[2].text=''
this.nav_header[2].type=''
}

if(res.body.code==40000){
for (var i=0; i<res.body.list.album.length; i++) {
var imgItem = {src: res.body.list.album[i]}
this.album.push(imgItem)
}
}
})
},
methods:{
show (index) {
this.$refs.previewer.show(index)
},
manage(){
if(this.edit_code=="40012")
this.$router.push({path:"/company_album_modify", query:{id: this.$route.query.id}})
}
},
directives: {
TransferDom
},
}



