	import header_nav from '@/components/template/header'
	import uploader from '@/components/template/uploader-1.vue'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import rowImg from '../../../static/img/jinru@2x.png'
	
	export default{
		name: "news_publish",
		data(){
			return{
				nav_header: [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '发布', icon: '', type:'', class:'header_middle'},
					{text: '', icon: '', type:'', class:'header_right'},
				],
				circleId   : null,							//行业圈id
				pcontent   : null,							//发布内容
				imgUrlArr  : [],							//图片路径数组
				upImageUrl : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
				rowImg     : rowImg,
				category   : null,							//分类数据
				categoryId : null,							//分类id
			}
		},
		components:{header_nav, uploader},
		created(){
			this.circleId = this.$route.query.cid?this.$route.query.cid:'';
			this.get_category()
		},
		methods:{
			//得到图片返回路径
			getImgUrl(_url){
				console.log(_url)
				this.imgUrlArr.unshift(_url)
			},
			//
			getBase64(data){
				//console.log(data)
			},
			//删除图片
			delImg(delSrc){
				var delIdx = this.imgUrlArr.indexOf(delSrc)
				this.imgUrlArr.splice(delIdx, 1)
			},
			//获取分类id
			getCategoryId(id){
				this.categoryId = id;
			},
			//获取分类
			get_category(){
				this.$http.get(process.env.BASE_URL+"/apps/circle/add",{
					params:{id: this.circleId}
				}).then(function(res){
					console.log(res)
					if(res.body.code==40000){
						this.category = res.body.list.category
					}else{
						this.$vux.toast.text("请登录", 'middle')
						this.$router.push({path: "/login", query:{app: 'login'}})
					}
				})
			},
			//发布
			publish(){
				this.$http.post(process.env.BASE_URL+"/apps/circle/add",{
					id         : this.circleId,
					content    : this.pcontent,
					img        : this.imgUrlArr.toString(),
					cid        : this.categoryId,
//					suffix     : 
//					data_id    : 
//					share_model: 
				}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code=="40000"){
		  				this.$router.push({path:"/discover_index"})
		  			}
				})
			}
		}
	}



