import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import zanImg from '../../../static/img/zan@2x.png'
import zanedImg from '../../../static/img/zan-selected@2x.png'

export default{
	name: "comment_detail",
	data(){
		return{
			nav_header: [
				{text: '', icon: backImg, type:'back', class:'header_left'},
				{text: '评论详情', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'share', class:'header_right'},
			],
			zanImg      : zanImg,
			zanedImg    : zanedImg,
			placeholder : null,			//评论区占位符
			news_data   : {},			//资讯详情
			comment_data: {},			//评论详情
			send_comment: null,			//评论内容
		}
	},
	components:{header_nav},
	created(){
		var id = this.$route.query.cid;
		this.getData(id)
	},
	methods:{
		//点赞
		praise(id, code){
			this._praise(id, code)
		},
		//删除自己评论
		delSelfCom(type, id, code){
			console.log(999)
			var that = this;
			var _url = "";
			if(type==1){
				_url = process.env.BASE_URL+"/apps/news/commentDel"
			}else if(type==2){
				_url = process.env.BASE_URL+"/apps/news/replyDel"
			}
			
			if(code=="40010"){
				that.$vux.confirm.show({
				  	title: '提示',
    				content: '确定删除该评论？',
				  	onCancel () {
				  		console.log("取消删除")
				  	},
				  	onConfirm () {
				  		that.$http.post(_url,{
				  			id: id
				  		}).then(function(res){
				  			console.log(res)
				  			that.$vux.toast.text(res.body.hint, 'middle')
				  			if(res.body.code==40000){
				  				that.getData(this.$route.query.cid)
				  			}
				  		})
				  	}
				})
			}
		},
		//点赞方法
		_praise(pid, code){
			var Url;
			if(code==40007){
				Url = process.env.BASE_URL+"/apps/news/commentPraiseAdd";
			}else{
				Url = process.env.BASE_URL+"/apps/news/commentPraiseDel"
			}
			this.$http.post(Url, {
				pid: pid,
			}).then(function(res){
				console.log(res)
				this.$vux.toast.text(res.body.hint, 'middle')
				if(res.body.code==40000){
					if(code==40007){
						this.comment_data.praise_code = 40008;
						this.comment_data.praise_count ++;
					}else{
						this.comment_data.praise_code = 40007;
						this.comment_data.praise_count --;
					}
				}
			})
		},
		//点击评论
		comment(){
			
		},
		//评论
		sendComment(){
			this.$http.post(process.env.BASE_URL+"/apps/news/reply",{
				pid    : this.comment_data.comment_id,
				content: this.send_comment
			}).then(function(res){
				console.log(res)
				this.$vux.toast.text(res.body.hint, 'middle')
				if(res.body.code=="40000"){
					this.send_comment = null;
					this.getData(this.$route.query.cid)
				}
			})
		},
		getData(id){
			this.$http.get(process.env.BASE_URL+"/apps/news/commentDetail",{
				params: {id: id}
			}).then(function(res){
				console.log(res)
				if(res.body.code==40000){
					this.news_data = res.body.list.news;
					this.comment_data = res.body.list.comment;
					this.placeholder = "回复："+res.body.list.comment.username
				}else if(res.body.code==70000){
					this.$router.go(-1)
				}
			})
		}
	}
}



