	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import shareImg from '../../../static/img/fenxiang@2x.png'
	import zanImg from '../../../static/img/zan@2x.png'
	import zanedImg from '../../../static/img/zan-selected@2x.png'
	import collectImg from '../../../static/img/shoucangStar@2x.png'
	import collectedImg from '../../../static/img/shoucang-selected@2x.png'
	
	export default{
		name:"detail_news",
		data(){
			return{
				nav_header: [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '资讯详情', icon: '', type:'', class:'header_middle'},
					{text: '', icon: collectImg, type:'collect', class:'header_right1'},
					{text: '', icon: shareImg, type:'share', class:'header_right'},
				],
				zanImg       : zanImg,
				zanedImg     : zanedImg,
				news_data    : {},			//资讯数据
				collectCode  : null,		//收藏吗
				comment_data : {},			//评论数据
				send_comment : null,		//发布评论数据
			}
		},
		components:{header_nav},
		
		created(){
			this.getData()
		},
		methods:{
			//收藏
			_collect(){
				this.collect_focus(1, this.news_data.news_id, this.news_data.title, this.collectCode)
			},
			//分享
			_share(){
				location.href = this.news_data.share_real_href;
			},
			//到公司
			toCompany(cid){
				this.$router.push({path: "space_detail", query:{cid: cid}})
			},
			//点赞
			praise(id, code, index){
				this._praise(id, code, index)
			},
			//删除自己评论
			delSelfCom(id){
				var that = this;
				that.$vux.confirm.show({
				  	title: '提示',
    				content: '确定删除该评论？',
				  	onCancel () {
				  		console.log("取消删除")
				  	},
				  	onConfirm () {
				  		that.$http.post(process.env.BASE_URL+"/apps/news/commentDel",{
				  			id: id
				  		}).then(function(res){
				  			console.log(res)
				  			that.$vux.toast.text(res.body.hint, 'middle')
				  			if(res.body.code==40000){
				  				that.getData()
				  			}
				  		})
				  	}
				})
			},
			//点击评论
			comment(id){
				this.$router.push({path: "/comment_detail", query:{cid: id}})
			},
			//评论
			sendComment(){
				this.$http.post(process.env.BASE_URL+"/apps/news/comment",{
					pid    : this.news_data.news_id,
					content: this.send_comment
				}).then(function(res){
					console.log(res,"0-0-0-0-0-0”")
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code=="40000"){
						this.send_comment = null;
						this.getData()
					}else{
						this.$vux.toast.text('评论失败', 'middle')
					}
				})
			},
			//收藏判断
			_collect_judge(){
				if(this.collectCode==40005){
					this.nav_header[2].icon = collectImg;
				}else{
					this.nav_header[2].icon = collectedImg;
				}
			},
			//收藏-关注
			collect_focus(type, id, title, code){
				var Url;
				if(code==40005){
					Url = process.env.BASE_URL+"/apps/general/collectAdd";
				}else{
					Url = process.env.BASE_URL+"/apps/general/collectDel"
				}
				
				this.$http.post(Url,{
					aid	  : id,
					title : title,
					favorite_category: type,
				},{emulateJSON:true}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						if(code==40005){
							this.collectCode = 40006;
						}else{
							this.collectCode = 40005;
						}
					}
					this._collect_judge()
				})
			},
			//点赞方法
			_praise(pid, code, index){
				var Url;
				if(code==40007){
					Url = process.env.BASE_URL+"/apps/news/commentPraiseAdd";
				}else{
					Url = process.env.BASE_URL+"/apps/news/commentPraiseDel"
				}
				this.$http.post(Url, {
					pid: pid,
				}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						if(code==40007){
							this.comment_data[index].praise_code = 40008;
							this.comment_data[index].praise_count ++;
						}else{
							this.comment_data[index].praise_code = 40007;
							this.comment_data[index].praise_count --;
						}
					}
				})
			},
			//请求页面数据
			getData(){
				this.$http.get(process.env.BASE_URL+"/apps/news/detailWeb",{
					params:{id: this.$route.query.id}
				}).then(function(res){
					console.log(res)
					if(res.body.code=="40000"){
						this.news_data = res.body.list.news;
						this.comment_data = res.body.list.comment;
						this.collectCode = res.body.list.news.collect_code;
						//判断是否收藏
						if(this.collectCode=="40006"){
							this.nav_header[2].icon = collectedImg
						}
					}
				})
			}
		},
		computed:{
			getContent(){
//				var data = this.news_data.content;
//				data.replace(/<img /g, "<img style='width:100%' ")
//				return data
			}
		}
	}



