export default{
	name: "preview_picture",
	data(){
		return{
			pageHeight : null,			//页面的高度
			imgHeight : null,			//拿到图票的高度
		}
	},
	props: ['imgSrc'],
	created(){
		var that = this;
		that.pageHeight = window.screen.availHeight;
		//解决安卓手机上图片的位置错位问题
		setTimeout(function(){
			that.$nextTick(() => {
				that.imgHeight = that.$refs.img.height;
				//console.log(that.imgHeight)
			})
		}, 50)
	},
	methods:{
		hidePreview(){
			this.$emit('hidePreview')
		}
	}
}



