	import header_nav from '@/components/template/header'
	import index_Bmap from '@/components/template/BMap'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import loactionImg from '../../../static/img/zhoubian--location@2x.png'
	import searchImg from '../../../static/img/sskuang-sousuo@2x.png'
	export default{
		name:"surround",
		data(){
			return{
				nav_header  : [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '全国', icon: loactionImg, type:'region', class:'header_left_1'},
					{text: '周边供求', icon: '', type:'', class:'header_middle'},
					{text: '', icon: searchImg, type:'search', class:'header_right'},
				], 
				categoryNav  : null, 		//分类导航
				category_idx : 0,			//分类下标
				supply_list  : [],			//供求列表
				page_num     : 1,			//加载页数
				newData      : [],			//新加载的数据
				noData       : null,
			}
		},
		components: {header_nav, index_Bmap},
		mounted(){
			this.$nextTick(function () {
				var map = new BMap.Map("allmap");
				var point = new BMap.Point(116.331398,39.897445);
				map.centerAndZoom(point,12);
			
				function myFun(result){
					var cityName = result.name;
					map.setCenter(cityName);
					console.log(result)
					localStorage.lng = result.center.lng;
	        		localStorage.lat = result.center.lat;
	        		localStorage.locationName = result.name;
				}
				var myCity = new BMap.LocalCity();
				myCity.get(myFun);
		    })
		},
		created(){
			this.getData(this.page_num)
		},
		methods:{
			topClick(type){
				if (type=='back') {
					this.$router.go(-1)
				}else if(type=="search"){
					this.$router.push({path: "/search"})
				}
			},
			categoryClick(index, id, type){
				this.page_num = 1;
				this.supply_list = [];
				this.category_idx = index;
				this.category = id;
				this.type = type;
				this.getData(this.page_num)
			},
			detail(id, type){
				this.$router.push({path:"/info_detail", query:{id: id, type: type}})
			},
			infinite(done) {
                console.log(this.noData)
                //没有更多数据
                if(this.newData.length==0) {
	                setTimeout(()=>{
	                    this.$refs.myscroller.finishInfinite(2);
	                })
	                return;
                }
                this.page_num ++;
                let self = this;
				
                setTimeout(() => {
                	this.getData(this.page_num);
                    if(this.newData.length==0) {
                        self.noData = "没有更多数据"
                    }
                    self.$refs.myscroller.resize();
                    done()
                }, 500)

            },
            refresh(done) {
                console.log('refresh')
                setTimeout(() => {
		          	done()
		        }, 10)
            },
            getData(page){
            	this.$http.get(process.env.BASE_URL+"/apps/index/near",{
					params:{
						page: page,
						category: this.category,
						type    : this.type,
						lng : localStorage.lng,
						lat : localStorage.lat
					}
				}).then(function(res){
					console.log(res)
					if(res.body.code==40000){
						this.nav_header[1].text = res.body.list.city;;
						this.categoryNav = res.body.list.category;
						//this.supply_list.concat();
						this.newData = res.body.list.supply_list;
						for(var i=0; i<this.newData.length; i++){
							this.supply_list.push(this.newData[i])
						}
						//console.log(this.supply_list,"-=-=")
					}else if(res.body.code==70000){
						this.$router.push({path:"/index"})
					}
				})
			}
		},
		watch:{
			lng(n, o){
				console.log(n, o, "-0-0-0-0-0")
			}
		}
	}



