import Vue from 'vue'
import Router from 'vue-router'
import Resource from 'vue-resource'


import login from '@/components/login/login'              //会员登录
import register from '@/components/login/register'        //会员注册
import forgotten from '@/components/login/forgotten'      //忘记密码

import BtmNav from '@/components/has_footer'
//首页

import Index from '@/components/index/index'
import IndexMap from '@/components/index/index_map'
import IndexMsg from '@/components/index/index_message'
import SupplyDemand from '@/components/index/supply_demand'
import Surround from '@/components/index/surround'
import Search from '@/components/index/search'
import InfoDetail from '@/components/index/info_detail'
import PublishSupply from '@/components/index/publish_supply'
import PublishGoods from '@/components/index/publish_goods'
//发现
import DiscoverIndex from '@/components/discover/index'
import Publish from '@/components/discover/publish'
import DetailMessage from '@/components/discover/detail_message'
import DetailNews from '@/components/discover/detail_news'
import CommentDetail from '@/components/discover/comment_detail'
//空间站
import SpanceIndex from '@/components/space/index'
import SpaceDetail from '@/components/space/space_detail'
import CompanyIntro from '@/components/space/company_intro'
import CompanySupply from '@/components/space/company_supply'
import CompanyAlbum from '@/components/space/company_album'
import CompanyAlbumModify from '@/components/space/company_album_modify'
import CompanyNews from '@/components/space/company_news'
import PersonalSpaceEdit from '@/components/space/personal_space_edit' //个人空间信息编辑
import CompanySpaceEdit from '@/components/space/company_space_edit' //企业空间信息编辑

//我的
import MemberIndex from '@/components/member/member_index'  //我的
import CompanySpaceport from '@/components/member/company_spaceport'  //企业空间站开通
import NewsCollection from '@/components/member/news_collection' //资讯收藏
import NewsDetail from '@/components/member/news_detail' //资讯详情
import SupplyDemandColl from '@/components/member/supply_demand_coll' //商品收藏
import SpaceCollection from '@/components/member/space_collection' //空间详情
import UpUserInfo from '@/components/member/up_user_info' //个人信息修改
import PersonalSpace from '@/components/member/personal_space' //个人空间站开通
import NotOpenSpace from '@/components/member/not_open_space' //未开通
import NotPayment from '@/components/member/not_payment' //付款
import PublishSupplyDemand from '@/components/member/publish_supply_demand' //发布供求
import AboutApp from '@/components/member/about_app' //关于app
import AboutUs from '@/components/member/about_us' //关于我们
import CPersonList from '@/components/member/cperson_list' //关注的人列表页
import CPersonDetail from '@/components/member/cperson_detail' //关注的人列表页
import MyPublish from '@/components/member/my_publish' //我的发布
import Feedback from '@/components/member/feedback' //意见反馈



Vue.use(Router)
Vue.use(Resource)
 
export default new Router({
  routes: [
	{path:'*', redirect: '/index' },
  	{path: '/login', name: 'login', component: login },									//登录
  	{path: '/register', name: 'register', component: register },						//注册
  	{path: '/forgotten', name: 'forgotten', component: forgotten },						//忘记密码
  	
  	{path: '/btm_nav', name: 'BtmNav', component: BtmNav, children: [
		{path: '/index', name: 'Index', component: Index},								//首页
		{path: '/discover_index', name: 'DiscoverIndex', component: DiscoverIndex },	//发现
		{path: '/spance_index', name: 'SpanceIndex', component: SpanceIndex },			//空间站
		{path: '/member_index', name: 'member_index', component: MemberIndex },			//我的
  	]},
  	//首页
    {path: '/index_map', name: 'IndexMap', component: IndexMap },						//首页地图
    {path: '/index_message', name: 'IndexMsg', component: IndexMsg },					//首页消息
    {path: '/supply_demand', name: 'Supply_demand', component: SupplyDemand },			//供求信息
    {path: '/surround', name: 'Surround', component: Surround },						//周边求购
    {path: '/search', name: 'Search', component: Search },								//周边求购搜索
    {path: '/info_detail', name: 'InfoDetail', component: InfoDetail },					//供求信息详情
    {path: '/publish_supply', name: 'PublishSupply', component: PublishSupply },		//供应发布
    {path: '/publish_goods', name: 'PublishGoods', component: PublishGoods },			//货源发布
    //发现
    {path: '/publish', name: 'Publish', component: Publish },							//发布资讯
    {path: '/detail_message', name: 'DetailMessage', component: DetailMessage },		//信息详情
    {path: '/detail_news', name: 'DetailNews', component: DetailNews },					//资讯详情
    {path: '/comment_detail', name: 'CommentDetail', component: CommentDetail },		//资讯详情
    //空间站
    {path: '/space_detail', name: 'SpaceDetail', component: SpaceDetail },				//空间站主页
    {path: '/company_intro', name: 'CompanyIntro', component: CompanyIntro },			//公司简介
    {path: '/company_supply', name: 'CompanySupply', component: CompanySupply },		//公司求购信息
    {path: '/company_album', name: 'CompanyAlbum', component: CompanyAlbum },			//公司相册
    {path: '/company_album_modify', name: 'CompanyAlbumModify', component: CompanyAlbumModify },	//公司相册管理
    {path: '/company_news', name: 'CompanyNews', component: CompanyNews },        //公司资讯中心
    {path: '/personal_space_edit', name: 'PersonalSpaceEdit', component: PersonalSpaceEdit },        //个人空间信息编辑
    {path: '/company_space_edit', name: 'CompanySpaceEdit', component: CompanySpaceEdit },        //企业空间信息编辑 
    
    //我的
    {path: '/company_spaceport', name: 'CompanySpaceport', component: CompanySpaceport }, //空间站开通
    {path: '/news_collection', name: 'NewsCollection', component: NewsCollection }, 	//资讯收藏
    {path: '/news_detail', name: 'NewsDetail', component: NewsDetail }, //资讯详情
    {path: '/supply_demand_coll', name: 'SupplyDemandColl', component: SupplyDemandColl }, //资讯收藏
    {path: '/space_collection', name: 'SpaceCollection', component: SpaceCollection }, //空间收藏
    {path: '/up_user_info',name:'UpUserInfo',component:UpUserInfo},//用户信息修改
    {path: '/personal_space',name:'PersonalSpace',component:PersonalSpace},//用户信息修改
    {path: '/not_open_space',name:'NotOpenSpace',component:NotOpenSpace},//未开通空间站
    {path: '/not_payment',name:'NotPayment',component:NotPayment},//未支付页面
    {path: '/publish_supply_demand',name:'PublishSupplyDemand',component:PublishSupplyDemand},//发布供求
    {path: '/about_app',name:'AboutApp',component:AboutApp},//关于app
    {path: '/about_us',name:'AboutUs',component:AboutUs},//关于我们
    {path: '/cperson_list',name:'CPersonList',component:CPersonList},//关注的人列表
    {path: '/cperson_detail',name:'CPersonDetail',component:CPersonDetail}, //关注的人详情
    {path: '/my_publish',name:'MyPublish',component:MyPublish},//我的发布
    {path: '/feedback',name:'Feedback',component:Feedback},//意见反馈
  ]
})



