import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
export default{
	name: "company_intro",
	data(){
		return{
			nav_header: [
				{text: '', icon: backImg, type:'back', class:'header_left'},
				{text: '', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'share', class:'header_right'},
			],
			page_height : null,		//页面高度
			company_data: {},
			collectCode : null,		//收藏码
			collectImg  :'../../../static/img/shoucangStar@2x.png',
			collectedImg:'../../../static/img/shoucang-selected@2x.png'
		}
	},
	components:{header_nav},
	
	created(){
		this.page_height = window.screen.availHeight-111 +'px';
		console.log(this.page_height)
		var id = this.$route.query.id;
		this.$http.get(process.env.BASE_URL+"/apps/company/introduction",{
			params:{ id: id }
		}).then(function(res){
			console.log(res)
			if (res.body.code==40000) {
				this.company_data = res.body.list;
				this.nav_header[1].text = res.body.list.name;
				this.collectCode = res.body.list.collect_code;
			}
		})
	},
	methods:{
		_collect(){
			this.collect(3, this.company_data.company_id, this.company_data.name, this.collectCode)
		},
		//收藏-关注
		collect(type, id, title, code){
			var Url;
			if(code==40005){
				Url = process.env.BASE_URL+"/apps/general/collectAdd";
			}else{
				Url = process.env.BASE_URL+"/apps/general/collectDel"
			}
			
			this.$http.post(Url,{
				aid	  : id,
				title : title,
				favorite_category: type,
			},{emulateJSON:true}).then(function(res){
				console.log(res)
				this.$vux.toast.text(res.body.hint, 'middle')
				if(res.body.code==40000){
					if(code==40005){
						this.collectCode = 40006;
					}else{
						this.collectCode = 40005;
					}
				}
			})
		},
		toDetail(){
			this.$router.push({path: "/space_detail", query:{cid: this.$route.query.id}})
		}
	}
}



