import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import defaultImg from '../../../static/img/daixvfei@2x.png'   
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'back', class:'header_left'},
				{text: '编辑主页信息', icon: '', type:'', class:'header_middle'},
				{text: '完成', icon: '', type:'save', class:'header_right'},
			], 
			cid:null, //用户id
			list:{},
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		this.cid = this.$route.query.cid;  
		this.getData();
	},
	methods:{
		/**
		 * 获取关注的人的信息 
		 */
		getData:function(){
			var that = this;
			this.$http.get(process.env.BASE_URL+"/apps/member/spaceEdit",{
				params:{
					id    : that.cid
				}
			}).then(function(res){
				console.log(res)
				if(res.body.code==40000){
					that.list = res.body.list.company_info;
				}
			});
		},
		/**
		 * 保存空间信息 
		 */
		saveSpaceInfo(){
			console.log(1);
		},
	}
}



