	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import defaultImg from '../../../static/img/daixvfei@2x.png'   
	import notColl from '../../../static/img/wushangpinshoucang@2x.png'   

	export default{
		name:"news_collection",
		data(){
			return{
				nav_header: [
					{text: '', icon:backImg, type:'back', class:'header_left'},
					{text: '空间收藏', icon: '', type:'', class:'header_middle'},
					{text: '管理', icon: '', type:'delete', class:'header_right'},
				],
				defaultImg:defaultImg, //默认图片
				notColl:notColl, //没有收藏图片
				page_num:1, //当前页码
				isDel:true, //是否显示删除界面
				allDel:false, //全选删除按钮是否选中
				newData:[], //当前请求服务器的最新数据
				collData:[], //数据集合
				noData:false, 
				delArr:[], //要删除的数据集合
				show:false,
			}
		},
		mounted () { 
	  	},
		components: {header_nav},
		created(){ 
			this.getCollData();
		},
		methods:{
			getCollData(){
				var that = this;
				this.$http.get(process.env.BASE_URL+"/apps/member/collect",{
					params:{
						favorite_category: 3, 
						page    : that.page_num
					}
				}).then(function(res){ 
					if(res.body.code==40000){
						that.newData = res.body.list.collect;
						if(that.newData.length==0 && that.page_num ==1){
							that.noData = true;
						}
						for(var i=0; i<that.newData.length; i++){
							that.collData.push(that.newData[i]);
						}
					}
				})
			},
			goCollDetail(id){
				if(this.nav_header[2].text=="管理"){
					this.$router.push({path:"/space_detail", query:{cid: id}});
				}
			},
			manageNews(){ 
				if(this.isDel){
					if(!this.noData){
						this.isDel = false;
						this.nav_header[2].text="完成";
					} 
				}else{
					this.isDel = true; 
					this.nav_header[2].text="管理";
				} 
			}, 
			//全选删除的收藏
			allDelColl(){
				if(this.allDel){
					this.allDel=false;
					this.delArr = [];
				}else{
					this.allDel = true;
					this.delArr = [];
					for(var i=0;i<this.collData.length;i++){
						this.delArr.push(this.collData[i].collect_id);
					}
				}
			},
			//选择删除的收藏
			selDelColl(id){
				let delNum = this.delArr.indexOf(id);
				if(delNum ==-1){
					this.delArr.push(id);
				}else{
					this.delArr.splice(delNum,1);
					this.allDel = false;
				}
			},
			/**
			 * 删除收藏 
			 */
			delColl(){  
				let that = this;
				that.$vux.confirm.show({
			        title: '提示',
			        content: '确定要删除？', 
			        onCancel(){ 
			        },
			        onConfirm(){ 
			          	if(that.delArr.length>0){   
							that.$http.post(process.env.BASE_URL+"/apps/member/collectDel",{ 
									id: that.delArr 
							}).then(function(res){ 
								if(res.body.code==40000){
									that.$vux.toast.text(res.body.hint, 'middel');
									that.delSuccess();
								}else{
									that.$vux.toast.text(res.body.hint, 'middel');
								}
							})
						}else{
							that.$vux.toast.text('未选择删除的空间', 'middel');
						} 
			        }
		      	}) 
			},
			/**
			 * 数据处理，删除成功以后执行 
			 */
			delSuccess(){ 
				this.page_num = 1;
				this.collData = [];
				this.getCollData(); 
			},
			infinite(done) {  
				let that = this; 
                if(that.newData.length==0) {
	                setTimeout(()=>{
	                    that.$refs.myscroller.finishInfinite(2);
	                })
	                return;
                }
                that.page_num ++; 
				
                setTimeout(function(){
                	that.getCollData();	
                    // if(that.newData.length==0) {
                    //     that.noData = "没有更多数据";
                    // }
                    that.$refs.myscroller.resize();
                    done();
                }, 500); 
            },
            refresh(done) { 
          		setTimeout(function(){
		          	done();
		        }, 10);
            },
		}
	}



