import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import img1 from '../../../static/img/gongyingfabu@2x.png'
import img2 from '../../../static/img/huoyuanfabu@2x.png'
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'back', class:'header_left'},
				{text: '发布供求', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'', class:'header_right'},
			], 
			img1:img1,
			img2:img2,
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		
	},
	methods:{
		/**
		 * 调整到供应发布 
		 */
		goSupply(){
			this.$router.push({path:"publish_supply",query:{type:1}});
		},
		/**
		 * 调整到供应发布 
		 */
		goDemand(){
			this.$router.push({path:"publish_goods",query:{type:2}});
		},

	}
}



