
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default{
name:"company_supply",
data(){
return{
nav_header  : [
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '供求信息', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
uid          : null,		//用户id
category     : 0,
type         : 0,
categoryNav  : null, 		//分类导航
supply_list  : [],			//供求列表
page_num     : 1,			//加载页数
newData      : [],			//新加载的数据
noData       : null,
}
},
components: {header_nav, Tab, TabItem},
created(){
this.uid = this.$route.query.uid;
this.getData(this.page_num)
},
methods:{
onItemClick(category_id, type){

this.page_num = 1;
this.category = category_id;
this.type = type;
this.supply_list = [];
this.getData(this.page_num)
},
topClick(type){
if (type=='back') {
this.$router.go(-1)
}else if(type=="search"){
this.$router.push({path: "/search"})
}
},
detail(id, type){
this.$router.push({path:"/info_detail", query:{id: id, type: type}})
},
infinite(done) {
console.log(this.noData)

if(this.newData.length==0) {
setTimeout(()=>{
this.$refs.myscroller.finishInfinite(2);
})
return;
}
this.page_num ++;
let self = this;

setTimeout(() => {
this.getData(this.page_num);
if(this.newData.length==0) {
self.noData = "没有更多数据"
}
self.$refs.myscroller.resize();
done()
}, 500)

},
refresh(done) {
console.log('refresh')
setTimeout(() => {
done()
}, 10)
},
getData(page){
this.$http.get(process.env.BASE_URL+"/apps/supply/index",{
params:{
page    : page,
category: this.category,
type    : this.type,
uid     : this.uid
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.categoryNav = res.body.list.category;
this.newData = res.body.list.supply_list;
for(var i=0; i<this.newData.length; i++){
this.supply_list.push(this.newData[i])
}
}
})
}
},
}



