import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import img1 from '../../../static/img/kefudianhua@2x.png'
import img2 from '../../../static/img/zaixianlianzi@2x.png'
import img3 from '../../../static/img/guanyuwomen@2x.png'
import img4 from '../../../static/img/yijian@2x.png'
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'back', class:'header_left'},
				{text: '关于APP', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'', class:'header_right'},
			], 
			img1:img1,
			img2:img2,
			img3:img3,
			img4:img4,
			list:[], 
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		this.getData();
	},
	methods:{
		/**
		 * 获取数据
		 */
		getData(){
			var that = this;
			this.$http.get(process.env.BASE_URL+"/apps/member/about").then(function(res){
				if(res.body.code==40000){
					that.list = res.body.list;
				}
			})
		},
		/**
		 * 退出登录 
		 */
		logout(){
			localStorage.token="";
			//this.$router.push({path:"login"});
			location.href = this.list.real_href
		},
		/**
		 * 调整到关于我们 
		 */
		goAboutUS(){
			this.$router.push({path:this.list.about_weburl});
		},
		/**
		 * 调整意见反馈 
		 */
		goFeedback(){
			this.$router.push({path:"feedback"});
		}

	}
}



