	import header_nav from '@/components/template/header'
	import preview_picture from '@/components/template/preview_picture'
	import backImg from '../../../static/img/fanhui-@2x.png'
	import shareImg from '../../../static/img/fenxiang@2x.png'
	import zanImg from '../../../static/img/zan@2x.png'
	import zanedImg from '../../../static/img/zan-selected@2x.png'
	import collectImg from '../../../static/img/shoucang-@2x.png'
	import collectedImg from '../../../static/img/shoucang-selected_yellow@2x.png'
	import delImg from '../../../static/img/address_delet@2x.png'
	
	export default{
		name: "news_publish",
		data(){
			return{
				nav_header: [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '信息详情', icon: '', type:'', class:'header_middle'},
					{text: '', icon: '', type:'delComment', class:'header_right1'},
					{text: '', icon: shareImg, type:'share', class:'header_right'},
				],
				pageWidth   : null,			//屏幕宽度
				zanImg      : zanImg,
				zanedImg    : zanedImg,
				collectImg  : collectImg,
				collectedImg: collectedImg,
				message_id  : null,			//信息id
				message_data: {},			//信息数据
				previewShow : false,		//是否显示图片预览
				previewImg  : null,			//预览图片的路径
				placeholder : "评论...",		//评论区的占位符
				comments    : null,			//评论内容
				replyId     : null,			//评论id
				toFocus 	: false,		//自动聚焦与否
			}
		},
		components:{header_nav, preview_picture},
		created(){
			this.pageWidth = window.screen.availWidth;
			this.message_id = this.$route.query.id;
			this.getMsg();
		},
		methods:{
			//点击图像名称跳转到个人空间
			toSelfSpace(uid){
				this.$router.push({path:"/cperson_detail", query:{uid: uid}})
			},
			//分享
			_share(){
				location.href = this.message_data.share_real_href;
			},
			delMsg(){
				var that = this;
				that.$vux.confirm.show({
				  	title: '提示',
    				content: '确定删除该信息？',
				  	onCancel () {
				  		console.log("取消删除")
				  	},
				  	onConfirm () {
				  		that.$http.post(process.env.BASE_URL+"/apps/circle/del",{
				  			id: that.$route.query.id
				  		}).then(function(res){
				  			console.log(res)
				  			this.$vux.toast.text(res.body.hint, 'middle')
				  			if(res.body.code=="40000"){
				  				that.$router.push({path:"/discover_index"})
				  			}
				  		})
				  	}
				})
			},
			//公司页面
			toCompany(id){
				this.$router.push({path:"/space_detail", query:{cid: id}})
			},
			//打电话
			call(phone){
				location.href="tel:"+phone;
			},
			//评论
			comment(){
				this.toFocus = true;
				this.comments = null;
				this.replyId  = null;
				this.placeholder = "评论..."
			},
			//赞
			praise(pid, code){
				this._praise(pid, code)
			},
			//收藏
			collect(id, content, code){
				this.collect_focus(5, id, content, code)
			},
			//预览图片
			lookImg(src){
				//this.$router.push({path: "/preview_picture", query:{imgSrc: src}})
				this.previewShow = true;
				this.previewImg = src;
			},
			//隐藏预览
			previewHide(){
				this.previewShow = false;
			},
			//获取页面数据
			getMsg(){
				this.$http.get(process.env.BASE_URL+"/apps/circle/detailNew",{
					params:{
						circle_id: this.message_id
					}
				}).then(function(res){
					console.log(res)
					if(res.body.code==40000){
						this.message_data = res.body.list;
						if(this.message_data.delete_code=="40010"){
							this.nav_header[2].icon = delImg
						}
					}else if(res.body.code==70000){
						this.$router.go(-1)
					}
				})
			},
			//发送消息
			sendMsg(){
				this.$http.post(process.env.BASE_URL+"/apps/circle/comment",{
					pid     : this.$route.query.id,
					cid     : this.replyId ? this.replyId:0,
					content : this.comments,
				}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						this.toFocus = false;
						this.comments = null;
						this.replyId  = null;
						this.getMsg();
					}
				})
			},
			//回复评论的操作
			deal_reply(comment_id, replay_code, del_code, userName){
				//console.log(comment_id, replay_code, del_code)
				var that = this;
				if(del_code=="40010"){						//自己的评论删除操作
					that.$vux.confirm.show({
					  	title: '提示',
        				content: '确定删除该评论？',
					  	onCancel () {
					  		console.log("取消删除")
					  	},
					  	onConfirm () {
					  		that.delComment(comment_id)
					  	}
					})
				}else{										//评论别人的评论
					if(replay_code=="40014"){
						that.toFocus = true;
						that.placeholder = "回复"+userName+":";
						that.replyId = comment_id;
					}
				}
			},
			//点赞方法
			_praise(pid, code){
				var Url;
				if(code==40007){
					Url = process.env.BASE_URL+"/apps/circle/praiseAdd";
				}else{
					Url = process.env.BASE_URL+"/apps/circle/praiseDel"
				}
				this.$http.post(Url, {
					pid: pid,
				}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						if(code==40007){
							this.message_data.praise_code = 40008;
							this.message_data.praise_count ++;
						}else{
							this.message_data.praise_code = 40007;
							this.message_data.praise_count --;
						}
					}
				})
			},
			//收藏-关注
			collect_focus(type, id, title, code){
				var Url;
				if(code==40005){
					Url = process.env.BASE_URL+"/apps/general/collectAdd";
				}else{
					Url = process.env.BASE_URL+"/apps/general/collectDel"
				}
				
				this.$http.post(Url,{
					aid	  : id,
					title : title,
					favorite_category: type,
				},{emulateJSON:true}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						if(code==40005){
							this.message_data.collect_code = 40006;
						}else{
							this.message_data.collect_code = 40005;
						}
					}
				})
			},
			//删除评论
			delComment(id){
				this.$http.post(process.env.BASE_URL+"/apps/circle/commentDel",{
					id: id,
				}).then(function(res){
					console.log(res)
					this.$vux.toast.text(res.body.hint, 'middle')
					if(res.body.code==40000){
						this.getMsg()
					}
				})
			}
		},
		//自动聚焦自定义指令
		directives: {
		    focus: {
		        update : function (el, {value}) {
		            if (value) {
		                el.focus();
		            }
		        }
		    }
		},
	}



