
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
name: "footer",
data() {
return {
footerList    : null,		//底部栏数据
clickNavIndex : 0,			//点击的底部导航的小标
footerNavArr  : [
'/index',
'/discover_index',
'/spance_index',
'/member_index?app=login',
]
}
},
components: {
Tabbar,
TabbarItem,
},
created(){
var that = this;
switch (this.$route.path){
case "/index":
this.clickNavIndex = 0
break;
case "/discover_index":
this.clickNavIndex = 1
break;
case "/spance_index":
this.clickNavIndex = 2
break;
default:
this.clickNavIndex = 3
break;
}

that.$http.get(process.env.BASE_URL+'/apps/start/footer').then(function(res){
that.footerList = res.data.list
for (var i=0; i<this.footerList.length; i++) {
that.$set(that.footerList[i], "link", that.footerNavArr[i])
}
console.log(that.footerList)
});
},
methods: {
nav_change(idx){
console.log(idx)
this.clickNavIndex = idx;
}
}
}



