export default {
	name: 'indexMap',
	data() {
		return {
			pageHeight: null,
			address : null
		}
	},
	created() {
		//设置地图的高度
		this.pageHeight = window.screen.availHeight - 51+'px';
		this.$emit('address', this.address)
	},
	mounted() {
		// 百度地图API功能
		var map = new BMap.Map("allmap"); // 创建Map实例
		map.centerAndZoom(new BMap.Point(116.404, 39.915), 12); // 初始化地图,设置中心点坐标和地图级别
					
		map.addControl(new BMap.MapTypeControl());    //添加地图类型控件
		map.addControl(new BMap.NavigationControl()); //添加标准地图控件(左上角的放大缩小左右拖拽控件)  
		map.addControl(new BMap.ScaleControl());      //添加比例尺控件(左下角显示的比例尺控件) 
		map.setCurrentCity("北京"); // 设置地图显示的城市 此项是必须设置的
		
		var geolocation = new BMap.Geolocation();
		geolocation.getCurrentPosition(function(r){
			if(this.getStatus() == BMAP_STATUS_SUCCESS){
				var mk = new BMap.Marker(r.point);
				map.addOverlay(mk);
				map.panTo(r.point);
				this.address = r.address;
				console.log(r);
				console.log(this.address,"地址");
				localStorage.lng = r.longitude;
				localStorage.lat = r.latitude;
				localStorage.locationName = r.address.city;
			}
			else {
				console.log('failed'+this.getStatus());
			}        
		},{enableHighAccuracy: true})
	},
	methods: {
		
	},
	watch:{
		address(n,o){
			console.log(n,o,"-=-=-=-=")
		}
	}
}



