	export default{
		data(){
			return{
				
			}
		},
		props: ['nav_header'],
		created(){
			
		},
		methods:{
			topClick(type){
				if (type=='back') {
					this.$router.go(-1)
				}else if(type=='location'){
//					this.$router.push({path: "/index_map"})
					this.$router.push({path: "/surround"})
				}else{ 
					this.$emit(type)
				}
			}
		}
	}



