
import Swiper from 'vux/src/components/swiper/swiper.vue'

import header_nav from '@/components/template/header'

export default{
name: "index",
data(){
return {
msg_num			   : ""   ,	   //消息数量显示
nav_header         :  []  ,    //头部图标，头部标题数据
section_datas_one  :  []  ,    //主体数据
section_datas_two  :  []  ,    //主体小图标
carousel_list_data :  []  ,    //主体轮播图数据
header_class       :  ["header_left","header_middle","header_right"]  ,    //头部class属性数组
navIndex           :  0,		//底部导航选中下标
footerList         :  [], 	//底部导航数据
}
},
components: {Swiper, header_nav},

created(){
var that = this ;
console.log(process.env)
this.$http.get(process.env.BASE_URL+"/apps/index/index").then(function(res){
console.log(res)
that.msg_num = res.body.list.member_info.message_count;
that.nav_header = res.data.list.navigation.navigation_items //头部图标，头部标题数据

that.nav_header.forEach(function (el,index) {
that.nav_header[index].class = that.header_class[index]
})
that.section_datas_one = res.data.list.section[2].section_datas //主体数据
that.section_datas_two = res.data.list.section[1].section_datas //主体小图标
that.carousel_list_data = res.data.list.section[0].section_datas //主体小图标
});
},
methods:{

message(){
location.href = this.nav_header[2].real_href;
},

linkType : function (link_href) {
location.href = link_href
},

good_link : function (id,type,index) {
this.$router.push({path: 'supply_demand', query:{type: type, category:id, index:index}})
},
tabClick(title) {
console.log(title=="供应发布")
if(title=="供应发布"){
this.$router.push({path:"/publish_supply", query:{type:1}})
}else if(title=="周边供求"){
this.$router.push({path:"/surround"})
}else if(title=="货源发布"){
this.$router.push({path:"/publish_goods", query:{type:2}})
}
}
}
}



