	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
	export default{
		name:"index_message",
		data(){
			return {
				nav_header: [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '聊天列表', icon: '', type:'', class:'header_middle'},
					{text: '', icon: '', type:'', class:'header_right'},
				],
				page_num    : 1,				//加载页数
				newData     : [],				//新加载的数据
				noData      : null,
			}
		},
		components:{header_nav},
		created(){
			
		},
		methods:{
			getData(page){
				
			},
			infinite(done) {
                console.log(this.noData)
                //没有更多数据
                if(this.newData.length==0) {
	                setTimeout(()=>{
	                    this.$refs.myscroller.finishInfinite(2);
	                })
	                return;
                }
                this.page_num ++;
                let self = this;
				
                setTimeout(() => {
                	this.getData(this.page_num);
                    if(this.newData.length==0) {
                        self.noData = "没有更多数据"
                    }
                    self.$refs.myscroller.resize();
                    done()
                }, 500)

            },
            refresh(done) {
                console.log('refresh')
                setTimeout(() => {
		          	done()
		        }, 10)
            },
		}
	}



