
import header_nav from '@/components/template/header'
import uploader from '@/components/template/uploader.vue'
import backImg from '../../../static/img/fanhui-@2x.png'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import value2name from 'vux/src/filters/value2name.js'
import Datetime from 'vux/src/components/datetime/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'


export default{
name: "publish_goods",
data(){
return {
edit_id     : null,			//编辑传的id
user_id     : null,			//用户id
category    : 122,			//添加分类(默认122)
publish_type: null,			//发布类型
nav_header:[
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '货源发布', icon: '', type:'', class:'header_middle'},
{text: '', icon: '', type:'', class:'header_right'},
],
categoryNav  : null, 		//分类数据
category_idx : 0,			//分类下标
info_title   : null,		//信息标题
upImageUrl   : process.env.BASE_URL+"/app/index/upload",		//图片上传路径
getImageUrl  : null,		//图片返回路径
goods_name   : null,		//货物名称
goods_num    : null,		//货物数量
isDanger     : null,		//是否是危险品
user_name    : null,		//联系人
user_phone   : null,		//联系电话
addressData  : ChinaAddressV3Data,				//省市区地址
storage_time : null,		//仓储时间
storAddress  : [],			//仓储地址
storage_detail: null,		//仓库详细地址
storage_area : null,		//仓库面积
depot_content: null,		//服务内容
}
},
components: {header_nav, Group, XInput, XTextarea, XButton, uploader, XAddress, ChinaAddressV3Data, Datetime, XSwitch },

created(){
this.publish_type = this.$route.query.type;
this.getData()
},
methods:{

categoryClick(index, id, type){
this.category_idx = index;
this.category = id;
console.log(this.category)

this.storAddress = [];
this.getData()
},

getImgUrl(_url){
this.getImageUrl = _url;
},

publish(){
this._submit();
},

changeDate(val){
console.log(val)
},

getData(){
this.$http.get(process.env.BASE_URL+"/apps/supply/publish",{
params:{
id      : this.edit_id,
type    : this.publish_type,
category: this.category,
}
}).then(function(res){
console.log(res)
if(res.body.code==40000){
this.categoryNav = res.body.list.category;
this.user_id = res.body.list.section.uid;
}else if(res.body.code==70000){
this.$vux.toast.text(res.body.hint, 'middle')
this.$router.push({path:"/index"})
}
})
},

_submit(){
this.$http.post(process.env.BASE_URL+"/apps/supply/publish", {
address      : this.storage_detail,
category     : this.category,
depot_area   : this.storage_area,
depot_content: this.depot_content,
goods_name   : this.goods_name,
goods_num    : this.goods_num,
is_danger    : this.isDanger==true?1:0,
id      : this.edit_id,
image   : this.getImageUrl,
mobile  : this.user_phone,
name    : this.user_name,
regions1: this.storAddress,
time1   : this.storage_time,
title   : this.info_title,
type    : this.publish_type,
uid     : this.user_id,
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
this.$router.push({path:"/supply_demand", query:{index: 0}})
})
}
}
}



