	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
  export default{
    name :"forgotten",
    data(){
      return{
      	 	mphone_data     : ""   ,    //手机号码
      	 	password_data   : ""   ,    //密码
      	 	captcha         : "",				//验证码
	      	times           : 60   ,    //验证码初始时间
	        show_content    : true ,		//展示读秒发送还是发送验证码
	        html            : ""   ,    //绑定读秒发送
	        register_content: "发送验证码" , //绑定发送验证码
	        flag            : true,      //控制点击事件执行
	        isA             : true,          //判断是否为查看密码，还是关闭密码
					nav_header: [
								{text: '', icon:backImg, type:'back', class:'header_left'},
								{text: '忘记密码', icon: '', type:'', class:'header_middle'},
								{text: '', icon: '', type:'', class:'header_right'},
					], //头部数据
      }
    },
    components:{ header_nav },
    created:function () {
			
    },
    methods:{
    	/*点击获取验证码*/
      register_content_valid_btn:function () {
      	var that = this;
      	if(that.mphone_data){
  				that.show_content = false;
	        that.$http.post(process.env.BASE_URL+'/apps/register/smsVerify',{
	          mphone     : that.mphone_data,
	        },{emulateJSON:true}).then(function(res){
	        	//提示组件
	        	console.log(res)
	          that.$vux.toast.text(res.body.hint, 'middle')
	          if(res.body.code == 40000){
				        //倒计时
				        for(var i = 0 ; i <= 60; i++){
				        	window.setTimeout(function(){
				           if (that.times != 0) {
						            that.times--;
						            that.html = that.times + "秒后可重发";
						            that.flag = false;
						            //console.log(that.times)
				         		}else {
				         				that.html = "重新发送"
				             		that.times = 60;
				             		that.flag = true;
				        		}
				      		}, 1000 * i)
				        }	
	          }else{
	          	that.show_content = true;
	          }
	        });
      	}else{
      		that.$vux.toast.text("请输入手机号码", 'middle')
      	}
      },
      /*忘记密码请求数据*/
      repassword_btn:function () {
      	var that = this;
        this.$http.post(process.env.BASE_URL+'/apps/register/forgetPassword',{
        	uid      : 1,
					mphone   : that.mphone_data,
      		password : that.password_data,
      		captcha  : that.captcha
        }).then(function(res){
        	console.log(res)
          that.$vux.toast.text(res.body.hint, 'middle')
          if(res.body.code==40000){
          	this.$router.push({path:"/login", query:{app: 'login'}})
          }
        });
      },
      //控制是否查看密码
      see_password:function(){
        	if(this.isA==false){
        		this.isA=true;
        	}else{
        		this.isA=false
        	}
      },
    }
  }



