	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
	export default{
		name:"search",
		data(){
			return{
				nav_header  : [
					{text: '', icon: backImg, type:'back', class:'header_left'},
					{text: '供求信息', icon: '', type:'input', class:'header_middle'},
					{text: '搜索', icon: '', type:'goSearch', class:'header_right'},
				],
				category    : [
					{name: "供应", type:1},
					{name: "货源", type:2},
				],
				categoryid  : null,			
				categoryidx : 0,				//分类下标
				page_num    : 1,				//页码数
				search_type : 1,				//搜索类型
				searchInp   : null,				//搜索内容
				supply_list : [],				//搜索数据
				newData     : [],				//加载的新数据
				noData      : null,
			}
		},
		components: {header_nav},
		
		created(){
			this.getData(this.page_num, this.search_type);
		},
		methods:{
			topClick(type){
				if (type=='back') {
					this.$router.go(-1)
				}else if(type=="goSearch"){
					this.page_num  = 1;
					this.supply_list = [];
					this.getData(this.page_num, this.search_type);
				}
			},
			categoryClick(type, index){
				this.categoryidx = index;
				this.search_type = type;
				this.page_num  = 1;
				this.supply_list = [];
				this.getData(this.page_num, this.search_type);
			},
			detail(id){
				this.$router.push({path:"/info_detail", query:{id: id, type: this.search_type}})
			},
			getData(page, type){
            	this.$http.get(process.env.BASE_URL+"/apps/index/near",{
					params:{
						page: page,
						category: this.categoryid,
						title   : this.searchInp,
						type    : type,
						lng : localStorage.lng,
						lat : localStorage.lat
					}
				}).then(function(res){
					console.log(res)
					if(res.body.code==40000){
						this.newData = res.body.list.supply_list;
						for(var i=0; i<this.newData.length; i++){
							this.supply_list.push(this.newData[i])
						}
					}
				})
			},
			infinite(done) {
                console.log(this.noData)
                //没有更多数据
                if(this.newData.length==0) {
	                setTimeout(()=>{
	                    this.$refs.myscroller.finishInfinite(2);
	                })
	                return;
                }
                this.page_num ++;
                let self = this;
				
                setTimeout(() => {
                	this.getData(this.page_num, this.search_type);
                    if(this.newData.length==0) {
                        self.noData = "没有更多数据"
                    }
                    self.$refs.myscroller.resize();
                    done()
                }, 500)

            },
            refresh(done) {
                console.log('refresh')
                setTimeout(() => {
		          	done()
		        }, 10)
            },
		}
	}



