import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import defaultImg from '../../../static/img/daixvfei@2x.png'   
export default{
	name:"news_collection",
	data(){
		return{
			nav_header: [
				{text: '', icon:backImg, type:'back', class:'header_left'},
				{text: '详情信息', icon: '', type:'', class:'header_middle'},
				{text: '', icon: '', type:'', class:'header_right'},
			], 
			uid:null, //用户id
			list:{},
			link_url: null,		//跳转连接
		}
	},
	mounted () { 
  	},
	components: {header_nav},
	created(){ 
		this.uid = this.$route.query.uid;  
		this.getData();
	},
	methods:{
		/**
		 * 获取关注的人的信息 
		 */
		getData:function(){
			var that = this;
			this.$http.get(process.env.BASE_URL+"/apps/circle/memberInfo",{
				params:{
					uid    : that.uid
				}
			}).then(function(res){
				if(res.body.code==40000){
					that.list = res.body.list.memberInfo;
					this.link_url = res.body.list.real_href;
				}
			});
		},
		/**
		 * 取消关注
		 */
		cancelCp(type){ 
			var that = this, _url;
			console.log(type)
			if(type==0){
				_url = process.env.BASE_URL+"/apps/general/collectAdd"
			}else if(type==1){
				_url = process.env.BASE_URL+"/apps/general/collectDel"
			}
			this.$http.post(_url,{ 
				title : this.list.name,
				aid   : that.uid,
				favorite_category: 8, //收藏分类，1=>资讯收藏，3=》空间站，5=》行业圈，6=》供应，8=》用户
			}).then(function(res){
				that.$vux.toast.text(res.body.hint, 'middel');
				if(res.body.code==40000){
					this.$router.push({path: "/cperson_list"})
				}
			});
		},
		//点击相册的跳转
		linkUrl(){
			location.href=this.link_url
		}
	}
}



