	import header_nav from '@/components/template/header'
	import backImg from '../../../static/img/fanhui-@2x.png'
  export default{
      name :"login",
      data(){
          return{
            mphone_data: "",    //手机号码
            password_data: "",  //密码
            isA: true,          //判断是否为查看密码，还是关闭密码
            nav_header: [
								{text: '', icon:backImg, type:'backs', class:'header_left'},
								{text: '会员登录', icon: '', type:'', class:'header_middle'},
								{text: '', icon: '', type:'', class:'header_right'},
						], //头部数据
          }
      },
      components:{ header_nav },
      created:function () {
					
      },
      methods:{
		backs(){
			this.$router.push({path:"/"})
		},
      	//点击登录按钮请求数据
        Login_success:function () {
          this.$http.post(process.env.BASE_URL+'/apps/login/login',{
              mphone : this.mphone_data,
              password : this.password_data,
          },{emulateJSON:true}).then(function(res){
          	this.$vux.toast.text(res.body.hint, 'middle')
          	console.log(res)
                if(res.body.code==40000){
                	localStorage.token=res.body.list.token;
                	this.$router.push({path:"/index"})
                }
          });
        },
        //控制是否查看密码
        see_password:function(){
        	if(this.isA==false){
        		this.isA=true;
        	}else{
        		this.isA=false
        	}
        },
      }
  }



