
import header_nav from '@/components/template/header'
import backImg from '../../../static/img/fanhui-@2x.png'
import shareImg from '../../../static/img/fenxiang@2x.png'
import collectImg from '../../../static/img/shoucangStar@2x.png'
import collectedImg from '../../../static/img/shoucang-selected@2x.png'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default{
name: "space_detail",
data(){
return{
nav_header: [
{text: '', icon: backImg, type:'back', class:'header_left'},
{text: '', icon: '', type:'', class:'header_middle'},
{text: '', icon: collectImg, type:'collect', class:'header_right1'},
{text: '', icon: shareImg, type:'share', class:'header_right'},
],
cid          : null,		//空间id
share_url    : null,		//分享连接
edit_url     : null,		//编辑连接
company_info : {},			//公司信息
category_list: {},			//分类信息
company_intro: null,		//公司简介
company_contact: {},		//公司地址
company_phone: null,		//公司电话
collectCode  : null,		//收藏吗
is_my        : null,        //是否为我的
}
},
components:{header_nav, Group, Cell},
created(){
this.cid = this.$route.query.cid;
this.is_my = this.$route.query.is_my;
this.type = this.$route.query.type;
this.$http.get(process.env.BASE_URL+"/apps/company/detail",{
params:{
id: this.cid
}
}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
this.share_url = res.body.list.share_href;
this.edit_url = res.body.list.real_href;
this.company_info = res.body.list.company_info;
this.category_list = res.body.list.company_navigation;
this.company_intro = res.body.list.company_introduction;
this.company_contact = res.body.list.company_contact;
this.company_phone = res.body.list.company_contact.telephone["0"];

this.collectCode = res.body.list.company_info.collect_code;
this._collect_judge()
}else if(res.body.code==70000){
this.$router.go(-1)
}
})
},
methods:{
phoneCall(){
location.href = "tel:"+this.company_phone;
},
_collect(){
this.collect_focus(3, this.company_info.company_id, this.company_info.name, this.collectCode)
},
_share(){
location.href = this.share_url;
},

_collect_judge(){
if(this.collectCode==40005){
this.nav_header[2].icon = collectImg;
}else{
this.nav_header[2].icon = collectedImg;
}
},

collect_focus(type, id, title, code){
var Url;
if(code==40005){
Url = process.env.BASE_URL+"/apps/general/collectAdd";
}else{
Url = process.env.BASE_URL+"/apps/general/collectDel"
}

this.$http.post(Url,{
aid	  : id,
title : title,
favorite_category: type,
},{emulateJSON:true}).then(function(res){
console.log(res)
this.$vux.toast.text(res.body.hint, 'middle')
if(res.body.code==40000){
if(code==40005){
this.collectCode = 40006;
}else{
this.collectCode = 40005;
}
}
this._collect_judge()
})
},
toZoon(){
this.$router.push({path:"/company_intro", query:{id: this.cid}})
},

sorts(flag, realUrl){
console.log(flag);
switch(flag){
case 'company_introduction':
this.$router.push({path:"/company_intro", query:{id: this.cid}})
break;
case 'company_product':
this.$router.push({path:"/company_supply", query:{uid: this.company_info.user_id}})
break;
case 'company_need':
this.$router.push({path:"/company_news", query:{uid: this.company_info.user_id}})
break;
case 'company_album':
this.$router.push({path:"/company_album", query:{id: this.cid}})
break;
case 'company_map':
location.href = realUrl
break;
default:
location.href = realUrl
}
},
/**
* 跳转到编辑主页
*/
goEditHome(){





location.href =  this.edit_url;
}
}
}



