import zImg from '../../../static/img/zhengmian@2x.png' 
import fImg from '../../../static/img/fanmian@2x.png' 
export default {
    props: {
        src: {
            type: String,
            required: true
        },
        bgType:{
            type: String,
            required: true
        }
    },
    data() {
        return {
            status: 'ready',
            files: [],
            point: {},
            uploading: false,
            percent: 0,
            zImg:zImg, 
            fImg:fImg,
        }
    }, 
    methods: {
        add() {
            this.$refs.file.click() 
        },
        submit() { 
            if (this.files.length === 0) {
                console.warn('no file!');
                return
            }
            const formData = new FormData()
            this.files.forEach((item) => {
                formData.append(item.name, item.file)
            })
            const xhr = new XMLHttpRequest()
            xhr.upload.addEventListener('progress', this.uploadProgress, false) 
            xhr.open('POST', this.src, true)
            this.uploading = true;
            xhr.send(formData);
            this.status = 'finished';
            xhr.onload = (res) => {
            	this.$vux.toast.text("图片上传中", 'middle')
                this.uploading = false
                if (xhr.status === 200 || xhr.status === 304) {
                	var imgUrl = JSON.parse(xhr.response).url; 
                    this.$vux.toast.text("图片上传完成", 'middle')
                    this.$emit('imgUrl', imgUrl)
                } else {
                    console.log(`error：error code ${xhr.status}`)
                }
            }
        },
        finished() {
            this.files = []
            this.status = 'ready'
        },
        remove(index) {
            this.files.splice(index, 1);
            //陶冶  2017.08.30
            //控制一张上传的（暂定）
            this.status = 'ready';
            this.$emit('removeImg',index);
        },
        fileChanged() {
            const list = this.$refs.file.files
            for (let i = 0; i < list.length; i++) {
                if (!this.isContain(list[i])) {
                    const item = {
                        name: list[i].name,
                        size: list[i].size,
                        file: list[i]
                    }
                    this.html5Reader(list[i], item)
                    this.files.push(item)
                }
            }
            this.$refs.file.value = '';
            
            //陶冶  2017.08.30
            //为自动上传添加
            this.submit()
        },
        // 将图片文件转成BASE64格式
        html5Reader(file, item){
            const reader = new FileReader()
            reader.onload = (e) => {
                this.$set(item, 'src', e.target.result)
            }
            reader.readAsDataURL(file)
        },
        isContain(file) {
            this.files.forEach((item) => {
                if(item.name === file.name && item.size === file.size) {
                    return true
                }
            })
            return false
        },
        uploadProgress(evt) {
            const component = this
            if (evt.lengthComputable) {
                const percentComplete = Math.round((evt.loaded * 100) / evt.total)
                component.percent = percentComplete / 100
            } else {
                console.warn('upload progress unable to compute')
            }
        }
    }
}



